import React, { Component, Fragment } from 'react'
import Head from 'next/head';
import '../static/css/style.scss'

export default class Layout extends Component {
    constructor(props) {
        super(props)

        this.state = {
            backTopShow: false
        }

        this.handleScroll = this.handleScroll.bind(this)
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll, true)
    }

    handleScroll() {
        let top = document.body.scrollTop
        console.log(top);
        if (top > 300) {
            this.setState({
                backTopShow: true
            })
        } else {
            this.setState({
                backTopShow: false
            })
        }
    }
    
    handleScrollTop() {
        window.scrollTo({
            left: 0,
            top: 0,
            behavior: 'smooth',
        });
    }
  
    render() {
        let {backTopShow} = this.state

        console.log(backTopShow);
        
        return (
            <Fragment>
                <Head>
                    <title>{this.props.title}</title>
                </Head>
                {this.props.children}
                <div id="back_top" onClick={this.handleScrollTop}><i className="iconfont icon-yooxi"></i></div>
            </Fragment>
        )
    }
}
